body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: left;
}

.hero-title{
  color: #3F93F5;
  font-family: Avenir, sans-serif; 
  font-size: 50px;
  font-weight: bolder;
  text-align: left;
  position: flex;
  padding-left: 65px;
}

.websiteHeader{
position: relative; 
height: 325px;
width: 1450px;
padding-top: 3px;
}

.selling-points{
  -webkit-column-count: 3;
          column-count: 3;
  grid-column-gap: 200px;
  -webkit-column-gap: 200px;
          column-gap: 200px;
  -webkit-column-rule-color: #FF51BA;
          column-rule-color: #FF51BA;
  -webkit-column-rule-style: solid;
          column-rule-style: solid;
  -webkit-column-rule-width: 1.5px;
          column-rule-width: 1.5px;
 padding-left: 75px;
}

.point{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
  color: #3F93F5;
}

.point-title{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 22px;
  color:rgb(0, 140, 255);
 position: relative;
}

/* Library.js styles */
.albumCover{
  position: relative;
  height: 25%;
  width: 25%;
  float: left;
  padding: 50px 200px 200px 50px;
}

.albumTitle {
  position: absolute;
  float:left;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 17px;
  font-style: italic;
  top:240px;
  padding: 0px 100px 50px 30px;
  color: #FF51BA;
  background-color:rgba(0, 0, 0, .7);
}

.albumInfo{
  position:absolute;
  float:left;
  padding: 50px 0px 50px 50px;
  top:5px;
  left:0px;
}

/* Album.js styles */

#album-cover-art{
  position: relative;
  float: left;
  padding: 50px 200px 200px 50px;
}

#album-title{
  position: absolute;
  float:left;
  font-family: Arial,sans-serif;
  font-size: 25.5px;
  font-style: italic;
  color: #FF51BA;
  padding: 21px 100px 70px 30px;
  margin: 240px 20px 20px 50px;
  background-color:rgba(0, 0, 0, .7);
}

#artist{  
  position:absolute;
  float:left;
  font-family:Arial, sans-serif;
  font-size: 17px;
  font-style: italic;
  font-weight: 100;
  padding: 281px 0px 50px 100px; 
  color: #FF51BA;
}

#release-info{
  position:absolute;
  float:left;
  font-size: 17px;
  font-style: italic;
  top:600px;
  padding: 85px 0px 0px 100px; 
  color: #FF51BA;
}

#song-list{
  font-family: Arial,sans-serif;
  position: absolute;
  color: #FF51BA;
  padding: 25px 25px 25px 14px;
  margin: 240px 50px 25px 478px;
  background-color: rgba(0, 0, 0, .7); 
}

#songIndex{
  position: absolute;
}

#songTitle{
  position: relative;
  padding: 0px 0px 0px 20px;
}

.player-bar{
  position: absolute;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  background-color: rgba(255, 255, 255, 0.5) ;
  color: rgb(255, 255, 255);
  margin: 468px 0px 0px 50px;
  padding: 50px 0px 0px 50px;
  width: 560px;
}

#buttons{
  position: absolute;
  margin:0px 0px 0px 0px;
}

.previous{

}

.play-pause{

}

.next{
  
}

#time-control{
position: absolute;
margin: 0px 0px 0px 90px;
}

.current-time{
  position: absolute;
  margin: 0px 0px 0px 0px; 
}

.seek-bar{
  position: absolute;
  width: 320px;
  margin: 0px 0px 0px 40px;
}

.total-time{
  position: absolute;
  margin: 0px 0px 0px 370px;
}

 #volume-control{
  position: relative;
  padding: 0px 0px 93px 100px;
}

 .volume-bar{
   position: absolute;
  width: 70px;
  margin:25px 0px 0px 35px; 
 }

.ion-md-volume-low{
  position: absolute;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  margin: 25px 0px 0px 0px;
} 

.ion-md-volume-high{
  position: absolute;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  padding: 25px 0px 0px 115px;
}
.nav-links{
  text-decoration: none;
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: .5px;
  text-align: left;
  color: white;
  position: relative;
  background-color: #3F93F5;
  padding-top: 12px;
  padding-left:85px;
  padding-bottom:10px;
  
}

a {
  text-decoration: none;
  color: rgb(255, 255, 255);
  text-align: left;
  padding-left:85px;
}

.footer{
  background-color: #3F93F5;
position: relative;
}
